/* Main feed styling */

a {
    text-decoration: none;
}

/* Desktop styling */
@media screen and (min-width: 650px) {
    nav {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 80px;
        border-right: 0.5px solid rgb(70, 70, 70);
        display: flex;
        flex-direction: column;
        justify-content: top;
        align-items: center;
        color: var(--main-accent-color);
    }

    #navbar-logo {
        font-size: 2em;
    }

    .navbar-button {
        width: 80%;
        height: 6%;
        background-color: var(--main-bg-color);
        color: var(--main-accent-color);
        border: none;
        border-radius: 5px;
        margin-top: 15px;
        margin-bottom: 15px;
        font-size: 1.2em;
        animation-name: fade-highlight-out;
        animation-duration: 0.15s;
    }

    .navbar-button:hover {
        animation-name: fade-highlight-in;
        animation-duration: 0.15s;
        background-color: rgb(70, 70, 70);
    }

    #navbar-logo {
        width: 80%;
        height: 6%;
        background-color: var(--main-bg-color);
        color: var(--main-accent-color);
        border: none;
        margin-top: 15px;
        margin-bottom: 15px;
        font-size: 2em;
    }

    .divider {
        width: 100%;
        height: 0.5px;
        background-color: rgb(70, 70, 70);
    }

    #top-menu {
        position: absolute;
        top: 0;
        left: 80px;
        height: 10%;
        width: calc(100% - 80px - 500px);
        border-bottom: 0.5px solid rgb(70, 70, 70);
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        color: var(--main-accent-color);
        font-size: 2em;
    }

    #news-panel {
        position: absolute;
        top:0;
        right: 0;
        width: 500px;
        height: 100%;
        border-left: 0.5px solid rgb(70, 70, 70);
        color: var(--main-accent-color);
        display: flex;
        flex-direction: column;
        justify-content: top;
        align-items: left;
    }

    #news-panel h1 {
        margin-left: 5%;
        font-size: 1.2em;
    }

    #post-button {
        position: absolute;
        bottom: 0;
        left: 0;
        margin-left: 15px;
        margin-bottom: 15px;
        width: 50px;
        height: 50px;
        background-color: var(--main-accent-color);
        color: var(--main-bg-color);
        font-size: 1.3em;
        border: none;
        border-radius: 50%;
        animation-name: post-button-normal;
        animation-duration: 0.25s;
    }

    /* Post button animations */
    @keyframes post-button-hover {
        from {
            margin-left: 15px;
            margin-bottom: 15px;
            width: 50px;
            height: 50px;
        }
        to {
            margin-left: 12.5px;
            margin-bottom: 12.5px;
            width: 55px;
            height: 55px;
        }
    }

    #post-button:hover {
        animation-name: post-button-hover;
        animation-duration: 0.25s;
        margin-left: 12.5px;
        margin-bottom: 12.5px;
        width: 55px;
        height: 55px;
    }

    @keyframes post-button-normal {
        from {
            margin-left: 12.5px;
            margin-bottom: 12.5px;
            width: 55px;
            height: 55px;
        }
        to {
            margin-left: 15px;
            margin-bottom: 15px;
            width: 50px;
            height: 50px;
        }
    }

    nav img {
        width: 20px;
        height: 20px;
        border-radius: 50%;
    }

    .headline-container {
        width: 450px;
        margin-left: 5%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        text-decoration: none;
        color: var(--main-accent-color);
        border-radius: 7px;
        border: none;
        margin-top: 10px;
        margin-bottom: 10px;
        padding-top: 15px;
        padding-bottom: 15px;
        animation-name: fade-highlight-out;
        animation-duration: 0.15s;
    }

    .headline-container:hover {
        background-color: var(--highlight-color);
        animation-name: fade-hightlight-in;
        animation-duration: 0.15s;
    }

    .headline-container img {
        width: 75px;
        border-radius: 5px;
        padding-right: 10px;
    }

    .headline-container h2 {
        font-size: 1em;
        padding-left: 10px;
        width: 60%;
    }

    @keyframes fade-highlight-in {
        from {
            background-color: var(--main-bg-color);
        } 
        to {
            background-color: var(--highlight-color);
        }
    }

    @keyframes fade-highlight-out {
        from {
            background-color: var(--highlight-color);
        }
        to {
            background-color: var(--main-bg-color);
        }
    }

}